import React from 'react';

import { getSelectedItemStyles } from './TypeaheadComboboxSelectedItem';
import { useDesignSystemTheme } from '../Hooks';

export interface CountBadgeProps extends React.HTMLAttributes<HTMLDivElement> {
  countStartAt?: number;
  totalCount: number;
}

export const CountBadge: React.FC<CountBadgeProps> = ({ countStartAt, totalCount }: CountBadgeProps) => {
  const { theme } = useDesignSystemTheme();
  return (
    <div css={[getSelectedItemStyles(theme), { paddingInlineEnd: theme.spacing.xs }]}>
      {countStartAt ? `+${totalCount - countStartAt}` : totalCount}
    </div>
  );
};
