import { InfoCircleOutlined } from '@ant-design/icons';

import type { TooltipProps } from './Tooltip';
import { Tooltip } from './Tooltip';
import { useDesignSystemTheme } from '../Hooks';
import { addDebugOutlineIfEnabled } from '../utils/debug';

export interface InfoTooltipProps extends Omit<React.HTMLAttributes<HTMLSpanElement>, 'title'> {
  title: React.ReactNode;
  tooltipProps?: Omit<TooltipProps, 'children' | 'title'>;
  iconTitle?: string;
  isKeyboardFocusable?: boolean;
}

export const InfoTooltip = ({
  title,
  tooltipProps,
  iconTitle,
  isKeyboardFocusable = true,
  ...iconProps
}: InfoTooltipProps): JSX.Element => {
  const { theme } = useDesignSystemTheme();
  return (
    <Tooltip title={title} {...tooltipProps}>
      {/*eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */}
      <span {...addDebugOutlineIfEnabled()} style={{ display: 'inline-flex' }} tabIndex={isKeyboardFocusable ? 0 : -1}>
        <InfoCircleOutlined
          aria-hidden="false"
          aria-label={iconTitle}
          css={{ fontSize: theme.typography.fontSizeSm, color: theme.colors.textSecondary }}
          {...iconProps}
        />
      </span>
    </Tooltip>
  );
};
