import { css } from '@emotion/react';

import { useTypeaheadComboboxContext } from './hooks/useTypeaheadComboboxContext';
import { importantify } from '../../design-system/utils/css-utils';
import type { ButtonProps } from '../Button';
import { Button } from '../Button';
import { useDesignSystemTheme } from '../Hooks';
import { PlusIcon } from '../Icon';
import { getComboboxOptionItemWrapperStyles } from '../_shared_/Combobox';

export const TypeaheadComboboxAddButton = ({ children, ...restProps }: ButtonProps) => {
  const { theme } = useDesignSystemTheme();
  const { isInsideTypeaheadCombobox } = useTypeaheadComboboxContext();

  if (!isInsideTypeaheadCombobox) {
    throw new Error('`TypeaheadComboboxAddButton` must be used within `TypeaheadCombobox`');
  }

  return (
    <Button
      {...restProps}
      type="tertiary"
      onClick={(event) => {
        event.stopPropagation();
        restProps.onClick?.(event);
      }}
      onMouseUp={(event) => {
        event.stopPropagation();
        restProps.onMouseUp?.(event);
      }}
      className="combobox-footer-add-button"
      css={{
        ...getComboboxOptionItemWrapperStyles(theme),
        ...css(
          importantify({
            width: '100%',
            padding: 0,
            display: 'flex',
            alignItems: 'center',
            borderRadius: 0,

            '&:focus': {
              background: theme.colors.actionTertiaryBackgroundHover,
              outline: 'none',
            },
          }),
        ),
      }}
      icon={<PlusIcon />}
    >
      {children}
    </Button>
  );
};
